/* eslint-disable no-dupe-keys */
import React, { useContext, useState} from 'react'
import { StudentContext } from '../Login/Student/NewStudent';
import Student from '../Login/Student/Student';
import StudentRegister from '../Login/Student/StudentRegister'
import Body from '../Other/Body'
import './stylesheet/Book.css'
import AddAPhotoIcon from '@material-ui/icons/AddAPhoto';
import { db, storage } from '../../Fire';
import makeid from './helper/Function';
import firebase from 'firebase'
import Feed from './Feed/Feed';
import Filter from './Filter/Filter';

const Book = () => {
    const [newStudent, setNewStudent] = useContext(StudentContext).newStudent;
    const [caption, setCaption] = useState("");
    const [price, setPrice] = useState("");
    const [image, setImage] = useState(null)
    const [progress, setProgress] = useState(0)

    const handleChange = (e) =>{
        if(e.target.files[0]){
            setImage(e.target.files[0] );

            
            var selectedImgSrc = URL.createObjectURL(e.target.files[0]);

            var imagePreview = document.getElementById('create_image')

            imagePreview.src = selectedImgSrc;
            imagePreview.style.display = "block"
        }
    }
    const uploadBtn = () =>{
        if(image){
            var imageName = makeid(10)
            const uploadImage = storage.ref(`image/${imageName}.jpg`)
            .put(image);

            uploadImage.on("state_changed", (snapshot) =>{
                const progress = Math.round((snapshot.bytesTransferred/snapshot.totalBytes)*100)
                setProgress(progress)

            }, (error) =>{
                console.log(error);
            }, () =>{


                storage.ref('image').child(`${imageName}.jpg`).getDownloadURL()
                .then((imageUrl) =>{
                    db.collection("posts").add({
                        timestamp:firebase.firestore.FieldValue.serverTimestamp(),
                        caption:caption,
                        photoUrl:imageUrl,
                        username:"student",
                        profileUrl:"https://pupexamination.ac.in/Images/logo.png",
                        price:price
                    })
                });
                setCaption("");
                setProgress(0);
                setImage(null);
                setPrice("")
                document.getElementById("create_image").style.display = "none"
            })
            
        }
    }


    return (
        <div className='book__post'>
        <div className='sort_filter'>
           <Filter  />
        </div>
        <div >
        <div className='createPost container'>
           <div className='create__input'>
           <p>Upload your old book</p>
           <div className='text__areaBox'>
             <textarea className='create_post' value={caption} onChange={(event) => setCaption(event.target.value)} rows='3' placeholder='Enter your book discription here...'>
             </textarea>
             <textarea className='create_post price' value={price} onChange={(event) => setPrice(event.target.value)} rows='3' placeholder='Enter Your Price here...'>
             </textarea>
             <div>
                 <img id="create_image" alt='' />
             </div>
           </div>
           <div className='create__upload'>
            <label htmlFor='fileInput'>
                <AddAPhotoIcon className='icon__upload' />
            </label>
               <input id='fileInput' type='file' accept='image/*' onChange={handleChange} />
               <button className='create__btn' style={{color:caption? "#000" : "#f3f3f3"}} onClick={uploadBtn}>{`Upload ${progress != 0 ? progress : ""}`}</button>
           </div>
           
           </div>
           
        </div>
        <div className='post__list container'>
            <Feed />
        </div>
        </div>
        </div>
    )
}

export default Book
