import React from 'react'
import * as ReactBootstrap from 'react-bootstrap'
import './stylesheet/Navbar.css'
import Logo from '../content/Add.png'

function Navbar({teacher, student, studentLogin, teacherLogin, Chat, NewsFeed}) {

    return (
        <div className='navbar_group'>
        <ReactBootstrap.Navbar collapseOnSelect expand="lg" bg='000'  variant="dark">
        <a href='/'>
           <img src={Logo} alt='logo' className='mr-5'  />
           </a>  
            {/* <ReactBootstrap.Navbar.Brand href="/" className='mr-5'>QualityGeek.in</ReactBootstrap.Navbar.Brand> */}
            <ReactBootstrap.Navbar.Toggle aria-controls="responsive-navbar-nav" />
            <ReactBootstrap.Navbar.Collapse id="responsive-navbar-nav">
            <ReactBootstrap.Nav >
            <ReactBootstrap.Nav.Link href='/'>{teacher}</ReactBootstrap.Nav.Link>
            <ReactBootstrap.Nav.Link href='/student'>{student}</ReactBootstrap.Nav.Link>
            <ReactBootstrap.NavDropdown title="Login" id="collasible-nav-dropdown">
                <ReactBootstrap.NavDropdown.Item href="student">{studentLogin}</ReactBootstrap.NavDropdown.Item>
                <ReactBootstrap.NavDropdown.Item href="teacher">{teacherLogin}</ReactBootstrap.NavDropdown.Item>
                <ReactBootstrap.NavDropdown.Item href="studentregister">Register</ReactBootstrap.NavDropdown.Item>
                </ReactBootstrap.NavDropdown>
           </ReactBootstrap.Nav>
          <ReactBootstrap.Nav>
        <ReactBootstrap.Nav.Link href="/join">{Chat}</ReactBootstrap.Nav.Link>
        <ReactBootstrap.Nav.Link eventKey={2} href="/book_post">{NewsFeed}</ReactBootstrap.Nav.Link>
            </ReactBootstrap.Nav>
        </ReactBootstrap.Navbar.Collapse>
        </ReactBootstrap.Navbar>

        </div>
    )
}

export default Navbar
