import React from 'react'
import * as ReactBootstrap from 'react-bootstrap'
import './stylesheet/Slider.css'
import image from '../content/college.jpg'
import library from '../content/library.jpg'
import girl from '../content/girl.jpg'
import students from '../content/students.jpg'
// import ground from '../content/ground.JFIF'

function Slider() {
    return (
        <div className='slider__carousel'>
                    <ReactBootstrap.Carousel fade>
            <ReactBootstrap.Carousel.Item>
                <img
                className="d-block w-100"
                src={image}
                alt="First slide"
                />
            </ReactBootstrap.Carousel.Item>
            <ReactBootstrap.Carousel.Item>
                <img
                className="d-block w-100"
                src={girl}
                alt="Second slide"
                />
            </ReactBootstrap.Carousel.Item>
            <ReactBootstrap.Carousel.Item>
                <img
                className="d-block w-100"
                src={students}

                // src={library}
                alt="Third slide"
                />
            </ReactBootstrap.Carousel.Item>
            </ReactBootstrap.Carousel>
            </div>
    )
}

export default Slider
