import React, { useState, useEffect } from 'react'
import 'bootstrap/dist/css/bootstrap.min.css'
import './App.css'
import Navbar from './components/Other/Navbar'
import Routing from './components/Login/Routing'
import Join from './components/SudentCHat/Chat/Join'
import { StudentContextProvider } from './components/Login/Student/NewStudent'
import SyncLoader from "react-spinners/SyncLoader";

const App = () => {
    const [student, setStudent] = useState('')
    const [loading, setLoading] = useState(false);

    useEffect(() =>{
        setLoading(true)
        setTimeout(()=>{
            setLoading(false)
        },2000)
    },[])
    return (
        <div className='App'>
        {
            loading  ?  
            <div className='loading'> <SyncLoader className='loader'   color={"#DD6711"}  loading={loading}  size={15} /> </div> : <StudentContextProvider>
            <div>
                 <Navbar teacher='Home' student='Student' studentLogin='Student' teacherLogin='Teacher' Chat='Senior Support' NewsFeed='Old Books'  />
             
                <Routing />
            </div>
            </StudentContextProvider>
        }
        </div>
    )
}

export default App
