import React, { useContext, useState } from 'react'
import { Link } from 'react-router-dom'
import '../stylesheet/login/Student.css'
import PermIdentityIcon from '@material-ui/icons/PermIdentity';
import LockIcon from '@material-ui/icons/Lock';
import axios from 'axios'
import {showMsgErr, showMsgSuccess} from '../../Other/utils/notification/Notification'
import { signInWithGoogle } from '../../auth';
import { StudentContext } from './NewStudent';

const initialState ={
    email:'',
    password:'',
    err:'',
    success:''
}

function Student() {
    const [newStudent, setNewStudent] = useContext(StudentContext).newStudent;
    const [students, setStudent] = useState(initialState)
    const {email, password, err, success} = students

    const handleChangeInput = e =>{
        const {name, value} = e.target
        setStudent({...students, [name]:value,err: '', success:''})
    }

    const handleSubmit = async e =>{
        e.preventDefault()
        try {
                const res = await axios.post('http://localhost:3000/student/studentslogin', {email, password}).then(() =>{
                console.log("successful")
                })
                setStudent({...students, err:'', success:res.data.msg})
                // setStudent({...students, err: '', success:res.message})
                console.log(res)
                localStorage.setItem('firstLogin', true)
        } catch (err) {
            console.log({err})
            err.response.data.msg && setStudent({...students,err: err.response.data.msg, success:''})
        }
    }


    const signInBtn = async () =>{
        let studentBySign = await signInWithGoogle() 
        if(studentBySign)  setNewStudent(studentBySign)
        console.log(studentBySign)
    };


    return (

        <div className='container form row'>
            <div className='col-md-6 col-12 student_first'>
                <img className='img-fluid' src='https://image.freepik.com/free-vector/geography-teacher-class_74855-5512.jpg' alt='img' />
                <div>
                       <Link to='/studentregister' className='student__link' >Create a new account</Link>
                </div>  
            </div>
            <div className='col-md-6 col-12 student_second'>
            <h1>Student Login</h1>
                {err && showMsgErr(err)}
                {success && showMsgSuccess(success)}
                <form onSubmit={handleSubmit}>
                    <div className='input'>
                        <label htmlFor='email' >Enter your Email</label><br/>
                        <PermIdentityIcon />
                        <input  type='email' id='email' placeholder='Enter your email' name='email' value={email}    onChange={handleChangeInput} />
                    </div>
                    <div className='input'>
                        <label htmlFor='password' >Enter your Password</label><br/>
                        <LockIcon />
                        <input type='password' id='password'  placeholder='Enter your password' name='password' value={password}  onChange={handleChangeInput} />
                    </div>
                    <div> 
                        <button  className='submit' type='submit' >Login</button>
                        <button onClick={signInBtn}>Sign in with Gooogle</button>
                    </div>
                </form>
            </div>
        </div>
    )
}

export default Student
