/* eslint-disable jsx-a11y/iframe-has-title */
import React from 'react'
import './stylesheet/Footer.css'
import MailOutlineIcon from '@material-ui/icons/MailOutline';
import LocationOnIcon from '@material-ui/icons/LocationOn';
import CallIcon from '@material-ui/icons/Call';
import FacebookIcon from '@material-ui/icons/Facebook';
import InstagramIcon from '@material-ui/icons/Instagram';

const Footer = () => {
    return (
        <>
        <div className='container footer__first'>
            <div className='row'>
                <div className='col-md-4 col-12 program address'>
                     
                    <div className='program__text'>
                    <LocationOnIcon className='program_icon' />
                        <h2>Address</h2>
                        <p>Govt Rajindra college near Bus stand bathinda 151001</p>
                    </div>
                </div>
                <div className='col-md-4 col-12 program address'>
                    
                    <div className='program__text'>
                    <CallIcon className='program_icon' />
                        <h2>Phone Number</h2>
                        <p>0164-2211983</p>
                        <p>0164-2211943</p>
                    </div>
                </div>
                <div className='col-md-4 col-12 program address'>
                  
                    <div className='program__text'>
                    <MailOutlineIcon className='program_icon' />
                        <h2>Email Address</h2>
                        <p>gcbathinda@gmail.com</p>
                    </div>
                </div>                
            </div>
        </div>
        <div className='footer__second'>
            <div className='container footer__container'>
            <div className='row'>
                <div className='col-md-6' >
                    <h2>About GRCB</h2>
                    <p>Government Rajindra College is one of the oldest institutions in the Malwa region, established in 1940. It was founded in memory of Maharaja Rajinder Singh. This college was established as a primary school in the year 1904 and turned into an Intermediate College in 1940.</p>
                </div>
                <div className='col-md-6 footer_map'>
                <h2>Location</h2>
                <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3448.2735309937207!2d74.94418511551876!3d30.20073961812612!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x39172d5f95fcc20d%3A0x7ed5c207976925fb!2sGovernment%20Rajindra%20College!5e0!3m2!1sen!2sin!4v1617128493273!5m2!1sen!2sin"    loading="lazy" ></iframe>
                </div>
            </div>
            </div>
            <div className='footer__owner'>
                <div>
                    <p>Created by Harmandeep</p>
                </div>
                <div className='footer__links'>
                    <a href='https://www.facebook.com/profile.php?id=100005664885002' ><FacebookIcon /></a>
                    <a href='https://www.instagram.com/im_paaras/' ><InstagramIcon /></a>
                </div>
            </div>
        </div>
        </>

    )
}

export default Footer
