import React, { useContext, useState } from 'react'
import { Link } from 'react-router-dom'
import '../stylesheet/login/Student.css'
import FontDownloadIcon from '@material-ui/icons/FontDownload';
import PermIdentityIcon from '@material-ui/icons/PermIdentity';
import ImportContactsIcon from '@material-ui/icons/ImportContacts';
import LockIcon from '@material-ui/icons/Lock';
import RecentActorsIcon from '@material-ui/icons/RecentActors';
import { signInWithGoogle } from '../../auth';
import { StudentContext } from './NewStudent';

function StudentRegister() {

    const [ setNewStudent] = useContext(StudentContext).newStudent;
    
    const signInBtn = async () =>{
        let studentBySign = await signInWithGoogle() 
        if(studentBySign)  setNewStudent(studentBySign);
        console.log(studentBySign);
    };
    return (
        <div className='container form row'> 
       
            <div className='col-md-6 col-12 student_second'>
            <h1>Student Regsiter</h1>
                <form>
                    <div className='input'>
                        <label>Enter your Name</label><br/>
                        <FontDownloadIcon />
                        <input required type='text' placeholder='Enter your name'  name='name' />
                    </div>
                    <div className='input'>
                        <label>Enter your Email</label><br />
                        <PermIdentityIcon />
                        <input required type='email' placeholder='Enter your Email'  name='email' />
                    </div>
                    <div className='input'>
                        <label>Enter your Course</label><br/>
                        <ImportContactsIcon />
                        <input required type='text' placeholder='Enter your course'  name='course' />
                    </div>
                    <div className='input'>
                        <label>Enter your ApplicationID</label><br/>
                        <RecentActorsIcon />
                        <input required type='text' placeholder='Enter your ApplicationId'  name='applicationId' />
                    </div>
                    <div className='input'>
                        <label>Enter your Password</label><br/>
                        <LockIcon />
                        <input required type='password' placeholder='Enter your password'  name='password' />
                    </div>
                    
                    <div>
                        <Link to='/' type='submit' className='submit' >Register</Link> <br /><br />
                        <button onClick={signInBtn} className='submit'>Sign in with Gooogle</button>
                    </div>
                   
                </form>
               
            </div>
            <div className='col-md-6 col-12 student_first'>
                <img className='img-fluid' src='https://image.freepik.com/free-vector/geography-teacher-class_74855-5512.jpg' alt='img' />
                <div>
                       <Link to='/student' className='student__link' >Already Register</Link>
                </div>  
        </div>
        </div>
    )
}

export default StudentRegister
