import React from 'react'
import { BrowserRouter, Route, Switch } from 'react-router-dom'
import Teacher from './Teacher/Teacher'
import Student from './Student/Student'
import Error from '../Other/Error'
import Body from '../Other/Body'
import StudentRegister from './Student/StudentRegister'
import TeacherRegister from './Teacher/TeacherRegister'
import Chat from '../SudentCHat/Chat/Chat'
import Logged from './Student/Logged'
import Join from '../SudentCHat/Chat/Join'
import Book from '../PostingBook/Book'

function Routing() {
    return (
        <div>
       <BrowserRouter>
            <Switch>
            <Route path='/' component={Body} exact />
                <Route path='/student' component={Student} />
                <Route path='/studentregister' component={StudentRegister} />
                <Route path='/teacherregister' component={TeacherRegister} />
                <Route path='/teacher' component={Teacher} />
                <Route path='/join' component={Join} />
                <Route path='/student_chat' component={Chat} />
                <Route path='/student_logged' component={Logged} />
                <Route path='/book_post' component={Book} />
                <Route component={Error} />
            </Switch>
        </BrowserRouter>
        </div>
    )
}

export default Routing
