import React, { useContext, useState } from 'react'
import './Comment.css'
import SendIcon from '@material-ui/icons/Send';
import { db } from '../../../Fire';
import { StudentContext } from '../../Login/Student/NewStudent';

const Comment = ({comments, id}) => {
    // const [student, setStudent] = useContext(StudentContext).student;
    const [comment, setComment] = useState("")
    const [commentArray, setCommmentArray] = useState(comments ? comments : [])

    const commentUpload = () =>{
       if(comment != ""){
            commentArray.push({
                comment:comment,
                username:"student"
        })
        db.collection("posts").doc(id).update({
            comments:commentArray
        }).then(function (){
            setComment("");
            alert(`your comment successfull add`);
        }).catch((err) =>{
            alert(`Error${err}`)
        })
       }
    }
    return (
        <div className='comment__box'>
            <textarea className='comment__area' rows="1" placeholder="Discussion Here..." value={comment} onChange={(e) => setComment(e.target.value)} >

            </textarea>
            <button onClick={commentUpload}><SendIcon /></button>
        </div>
    )
}

export default Comment
