import React, { useEffect, useState } from 'react'
import { db } from '../../../Fire';
import Post from '../Post/Post'
import './Feed.css'

const Feed = () => {

    const [posts, setPosts] = useState([]);

    useEffect(() => {
       db.collection("posts").orderBy("timestamp").onSnapshot((snapshot) =>{
            setPosts(snapshot.docs.map((doc) =>({id:doc.id, post:doc.data()})))
       })
    }, [posts])

    return (
        <div className='feed__post container'>
            {/* <Post profileUrl="blob:http://localhost:3000/0727a68f-2bbd-4470-be88-c1010855807c" username='student' comments="this is good" photoUrl="blob:http://localhost:3000/1fdc035d-fdd0-47a9-ab1a-f4ad4173607f" caption="This is my first post" /> */}
            {posts.map(({id, post}) =>{
                return <Post  key={id} id={id} profileUrl={post.photoUrl} username={post.username} photoUrl={post.photoUrl} caption={post.caption} comments={post.comments} price={post.price} />
            })}
        </div>
    )
}

export default Feed
