import React,{useState, useContext} from 'react'
import { db, storage } from '../../../Fire';
import { StudentContext } from '../../Login/Student/NewStudent';
import Comment from './Comment';
import './Post.css'

const Post = ({profileUrl, username, id, photoUrl, caption, comments, price }) => {
    const [newStudent, setNewStudent] = useContext(StudentContext).newStudent;

    const deletePost  = () =>{
       var imageRef = storage.refFromURL(photoUrl);
       imageRef.delete().then(function () {
           console.log("image has from firestore deleted")
       }).catch((err) =>{
            console.log(`Error${err}`)
       })

    //    delete from firebase collection

       db.collection("posts").doc(id).delete().then(() =>{
            alert(`${username} your post has been deleted successfull!`)
        }).catch((err) =>{
            console.log(`Error${err}`)
        })
    }

    return (
        <div className='post__feed'>
            <div className='post__header container'>
                <div className='header__left'>
                    <img src={profileUrl} alt='student' />
                    <p>{username}</p>
                </div> 
                <div className='header__right'>
                    <button onClick={deletePost}>Delete</button>
                </div>           
            </div>
            <div className='post__data'>
                <p><span>{username} : </span>{caption}</p>
                {price ? <h2>Rs.{price}</h2> : <></>}
                
                <img src={photoUrl} alt='post' className='post__image img-fluid' />
            <div>
            
            {comments ? comments.map((comment) => <div className='post__comments'><span>{comment.username}</span> : <p>{comment.comment}</p></div>) : <p>No Discussion</p>}
            {newStudent ? <Comment comments={comments} id={id} /> : "" }
            
                {/* {comments ? comments.map((comment) => <div className='post__comments'> <span>{comment.username} : </span> <p>{comment.comment}</p></div>)   : <p>No Discussion</p>} */}
            </div>
            </div>
        </div>
    )
}

export default Post
