import React from 'react'
import './stylesheet/Messages.css'

const Message = ({message:{user, text}, name}) => {
       
    let isSendByCurrentUser = false
    const trimdName = name.trim().toLowerCase();

    // eslint-disable-next-line no-undef
    if(user === trimdName){
        isSendByCurrentUser= true;       
    }

        return (
            isSendByCurrentUser ? (
               <div className='messageContainer justifyEnd'>
                 <p className='sendText pr-10'>{trimdName}</p>
                 <div className='messageBox backgroundBlue'>
                     <p className='messageOwner colorWhite'>{text}</p>
                 </div>
               </div>
            ) 
            :(
                <div className='messageContainer justifyStart'>
                <div className='messageBox backgroudnLight'>
                    <p className='messageOwner colorDark'>{text}</p>
                </div>
                <p className='sendText pl-10'>{user}</p>
              </div>
            )
        )

}
export default Message
