import React from 'react'
import { Link } from 'react-router-dom'
import FontDownloadIcon from '@material-ui/icons/FontDownload';
import PermIdentityIcon from '@material-ui/icons/PermIdentity';
import HomeIcon from '@material-ui/icons/Home';
import LockIcon from '@material-ui/icons/Lock';
import RecentActorsIcon from '@material-ui/icons/RecentActors';

function TeacherRegister() {

    // const [name, setName] = useState('')
    // const [email, setEmail] = useState('')
    // const [department, setDepartment] = useState('')
    // const [id, setID] = useState('')
    // const [password, setPassword] = useState('')
    return (
        <div className='container form row'>
        
            <div  className='col-md-6 col-12 student_second'>
            <h1>Teacher Regsiter</h1>
            
                <form>
                    <div className='input'>
                        <label>Enter your Name</label><br/>
                        <FontDownloadIcon />
                        <input required type='text' placeholder='Enter your email'  name='name' />
                    </div>
                    <div className='input'>
                        <label>Enter your Email</label><br/>
                        <PermIdentityIcon />
                        <input required type='email' placeholder='Enter your Email'  name='email' />
                    </div>
                    <div className='input'>
                        <label>Enter your Department</label><br/>
                        <HomeIcon />
                        <input required type='text' placeholder='Enter your Department'  name='course' />
                    </div>
                    <div className='input'>
                        <label>Enter your ID</label><br/>
                        <RecentActorsIcon />
                        <input required type='text' placeholder='Enter your ID'  name='ID' />
                    </div>
                    <div className='input'>
                        <label>Enter your Password</label><br/>
                        <LockIcon />
                        <input required type='password' placeholder='Enter your password'  name='password' />
                    </div>
                    
                    <div>
                        <Link to='/' className='submit' type='submit' >Register</Link>
                    </div>
                </form>
            </div>
            <div className='col-md-6 col-12 student_first'>
                    <img className='img-fluid' src='https://image.freepik.com/free-vector/geography-teacher-class_74855-5512.jpg' alt='img' />
                    <div>
                        <Link to='/teacher' className='student__link' >Already Register</Link>
                    </div>  
            </div>
        </div>
    )
}

export default TeacherRegister
