import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import '../stylesheet/login/Student.css'
import PermIdentityIcon from '@material-ui/icons/PermIdentity';
import LockIcon from '@material-ui/icons/Lock';

const initialState ={
    email:'',
    password:'',
    err:'',
    success:''
}

const Teacher = () => {
    const [teacher, setTeacher] = useState(initialState)
    const {email, password, err, sucess} = teacher
    return (
        <div className='container teacher form row'>
         <div className='col-md-6 col-12 student_first'>
                <img className='img-fluid' src='https://image.freepik.com/free-vector/girl-writing-journal-diary_74855-7408.jpg' alt='img' />
                <div>
                    <Link to='/teacherregister' className='student__link' >Create a new account</Link>
                </div>  
            </div>
            <div  className='col-md-6 col-12 student_second'>
             <h1>Teacher Login</h1>
            <form>
                <div className='input'>
                    <label>Enter your Email</label> <br/>
                    <PermIdentityIcon />
                    <input required type='email' placeholder='Enter your email' value={email} name='email' />
                </div>
                <div className='input'>
                    <label>Enter your Password</label><br/>
                    <LockIcon />
                    <input required type='password' placeholder='Enter your password' value={password} name='password' />
                </div>
                <div>
                    <Link to='/student' type='submit' className='submit' >Login</Link>
                </div>
            </form>
        </div>
    </div>
    )
}

export default Teacher
