import React, {useState} from 'react'
import {Link} from 'react-router-dom'
import './stylesheet/Join.css'

const Join = () => {

    const [name, setName] = useState('')
    const [room, setRoom] = useState('')

    return (
        <div className='joinChatOuter container'>
            <div className='joinChatInner'>
                <h1>Conversation</h1>
                <div><input type='text' placeholder='Enter your name..' className='join_input' onChange={(event) => setName(event.target.value)} /></div>
                <div><input type='text' placeholder='Enter your Class...' className='join_input' onChange={(event) => setRoom(event.target.value)} /></div>
            <Link onClick={event => (!name || !room ) ? event.preventDefault():null} to={`/student_chat?name=${name} &room=${room}`}>
                <button>Sign in </button>
            </Link>
            </div>
        </div>
    )
}

export default Join
