import React,{useState, useEffect} from 'react'
import './Filter.css'

const Filter = ({state}) => {

    const [title, setTitle] = useState('')
    const [sort, setSort] = useState('')
    const [search, setSearch] = useState('')
    const [category, setCategory] = useState('')
    
    // const {category} = state


    const handleChange = (e) =>{
        const data= e.target.value;
        console.log(data)
        setSort(data);
    }


    return (
        <div className='input_group'>
           <div className='input_group-prepend'>
                <select value={sort} onChange={handleChange} className='custom-select' >
                    <option>All</option>
                    <option>BCA</option>
                    <option>B.COM</option>
                    <option>BA</option>
                    <option>BBA</option>
                    <option>B.SC</option>
                    <option>Diploma</option>
                    <option>M.A.</option>
                </select>
           </div>
           <div>
               <form>
                   <input type='text' list='title_product' value={search.toLowerCase()} />
                   <datalist className='title_product'>
                       <option>title name</option>
                   </datalist>
                   <button className='btn btn-info' type='submit'>Search</button>
               </form>
           </div>
        </div>
    )
}

export default Filter
